import { Component, OnInit, TemplateRef, OnDestroy } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataTableResource } from 'angular7-data-table';

import { LanguageService } from '../../services/language.service';
import { Subscription } from 'rxjs';

import { NotificationsService } from './notifications.service';
import { UsersService } from '../users/users.service';

import { FormBuilder, Validators, FormArray } from '@angular/forms';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.scss'],
})
export class NotificationsComponent implements OnInit, OnDestroy {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  private langSub: Subscription;
  currentLang;
  arabic: boolean;

  constructor(
    private fb: FormBuilder,
    private notificationService: NotificationsService,
    private usersService: UsersService,
    private modalService: BsModalService,
    private languageService: LanguageService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService
  ) {
    this.langSub = this.languageService.currentLanguage.subscribe((lang) => {
      this.currentLang = lang;
      if (this.currentLang && this.currentLang === 'ar') {
        this.arabic = true;
      } else {
        this.arabic = false;
      }
    });
  }
  ngOnDestroy(): void {
    this.langSub.unsubscribe();
  }

  ngOnInit(): void {
    this.getData();
  }

  getData() {
    this.spinner.show();

    this.items = [];
    this.tableElements = [];
    this.usersService.getAll();
    this.usersService.users.subscribe(async (data) => {
      if (data) {
        this.items = data;

        if (data.length > this.limits[0]) {
          this.limit = data.length;
          this.limits[0] = data.length;
        }

        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });

    this.spinner.hide();
  }

  async reloadItems(event) {
    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }

  async FilterRecords(FilterRecord) {
    this.tableElements = this.items;
    if (FilterRecord !== '') {
      this.tableElements = this.items.filter((item) => {
        if (FilterRecord.toLowerCase() === 'owner') {
          return item.owner;
        } else if (FilterRecord.toLowerCase() === 'shipper') {
          return item.shipper;
        } else if (FilterRecord.toLowerCase() === 'president') {
          return item.leader;
        } else if (FilterRecord.toLowerCase() === 'user') {
          return !item.owner && !item.shipper && !item.leader;
        } else {
          return (
            item.name.toLowerCase().includes(FilterRecord) ||
            item.phone?.toString().includes(FilterRecord) ||
            item.address?.toLowerCase().includes(FilterRecord) ||
            item.bikeMake?.toLowerCase().includes(FilterRecord) ||
            item.bikeYear?.toString().includes(FilterRecord) ||
            item.bikeModel?.toString().includes(FilterRecord)
          );
        }
      });
    }

    this.itemResource = new DataTableResource(this.tableElements);
    this.itemCount = await this.itemResource.count();
    this.reloadItems({
      offset: 0,
      limit: this.limit,
    });
  }

  selectedRows = [];

  fcmTokens = [];
  selectRow(event) {
    if (!event.row._selected) {
      this.selectedRows.push(event.row.item);
    } else {
      this.selectedRows.splice(this.selectedRows.indexOf(event.row.item), 1);
    }
  }

  form = this.fb.group({
    title: ['', Validators.required],
    message: ['', Validators.required],
  });
  sendNotification() {
    this.spinner.show();

    for (let index = 0; index < this.selectedRows.length; index++) {
      const row = this.selectedRows[index];

      if (row.fcmToken) {
        this.fcmTokens.push(row.fcmToken);
      }
    }

    let newForm = { ...this.form.value, fcmTokens: this.fcmTokens };

    if (this.fcmTokens.length === 0) {
      return this.toastr.error(
        'Select at least one user with available notification',
        'Error'
      );
    } else if (this.fcmTokens.length === 1) {
      this.notificationService.send(newForm).subscribe(
        (res) => {
          console.log(res);
          this.toastr.info(res['message'], 'Info');
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );
    } else {
      this.notificationService.sendMultiCast(newForm).subscribe(
        (res) => {
          console.log(res);
          this.toastr.info(res['message'], 'Info');
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );
    }

    this.selectedRows = [];
    this.fcmTokens = [];
    this.form.reset();
    this.getData();

    this.spinner.hide();
    this.modalRef.hide();
  }
}
